import React from 'react';
import { Link } from 'react-router-dom';
import './Nav.css';


function Nav() {


  return ( 
    <div className='container'>
      <nav className="navbar navbar-expand-lg navbar-light ">
        <Link to='/task' style={{textDecoration:'none'}} ><a className="navbar-brand" href="/task">BLOGINIK</a></Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto">
            <li className="nav-item active">
              <Link to='/task' style={{textDecoration:'none'}} ><a className="nav-link" href="/task">Home <span className="sr-only">(current)</span></a></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/postdetails' style={{textDecoration:'none'}} ><a className="nav-link" href="/task/postdetails">About</a></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/postdetails' style={{textDecoration:'none'}} ><a className="nav-link" href="/">Category</a></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/postdetails' style={{textDecoration:'none'}} ><a className="nav-link" href="/">Pages</a></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/postdetails' style={{textDecoration:'none'}} ><a className="nav-link" href="/">Layout</a></Link>
            </li>
            <li className="nav-item">
              <Link to='/task/postdetails' style={{textDecoration:'none'}} ><a className="nav-link" href="/">Contact</a></Link>
            </li>

          </ul>
        </div>
      </nav>
    </div>
  )
}

export default Nav;
